import { NextAuthOptions } from 'next-auth'
import { PrismaAdapter } from '@auth/prisma-adapter'
import GoogleProvider from 'next-auth/providers/google'
import { prisma } from './db'
import type { Session } from 'next-auth'

const isDev = process.env.NODE_ENV === 'development' && process.env.DEV_MODE === 'true'

// Get or create dev user for development
export async function getDevUser() {
  if (!isDev) return null
  
  let devUser = await prisma.user.findUnique({
    where: { email: 'dev@flowtd.local' }
  })
  
  if (!devUser) {
    devUser = await prisma.user.create({
      data: {
        email: 'dev@flowtd.local',
        name: 'Dev User',
        emailVerified: new Date(),
      }
    })
  }
  
  return devUser
}

// Helper to get session with dev mode support
export async function getServerSessionWithDev(options?: NextAuthOptions): Promise<Session | null> {
  if (isDev) {
    const devUser = await getDevUser()
    if (devUser) {
      return {
        user: {
          id: devUser.id,
          email: devUser.email,
          name: devUser.name,
          image: devUser.image,
        },
        expires: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString(), // 30 days
      } as Session
    }
  }
  
  // Fall back to normal NextAuth session
  const { getServerSession } = await import('next-auth/next')
  return getServerSession(options || authOptions)
}

export const authOptions: NextAuthOptions = {
  adapter: PrismaAdapter(prisma) as any,
  providers: [
    GoogleProvider({
      clientId: process.env.GOOGLE_CLIENT_ID!,
      clientSecret: process.env.GOOGLE_CLIENT_SECRET!,
      authorization: {
        params: {
          scope: 'openid email profile https://www.googleapis.com/auth/calendar.readonly https://www.googleapis.com/auth/calendar.events',
        },
      },
    }),
  ],
  callbacks: {
    async session({ session, user }) {
      if (session.user) {
        session.user.id = user.id
      }
      return session
    },
  },
  pages: {
    signIn: '/auth/signin',
  },
}

declare module 'next-auth' {
  interface Session {
    user: {
      id: string
      name?: string | null
      email?: string | null
      image?: string | null
    }
  }
}

